<template>
  <div class="p-home">
    <img class="bg" :src="`${$router.options.base}bg/home.png`">
    <h2 class="n" :class="`n${i + 1}`" v-for="(item, i) in list" v-bind:key="item.max">{{ localise(item.total) }}</h2>

    <div class="img">
      <img v-if="img % 3==0" :src="`${$router.options.base}bg/analysis-img-01-sml.jpg`">
      <img v-if="img % 3==1" :src="`${$router.options.base}bg/analysis-img-02-sml.jpg`">
      <img v-if="img % 3==2" :src="`${$router.options.base}bg/analysis-img-03-sml.jpg`">
    </div>

    <router-link :to="{ name: 'analysis' }"><span>Go</span></router-link>
    <div class="refresh" @click="++img">
      <img :src="`${$router.options.base}refresh.svg`" draggable="false">
      <br>Next image
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      img: 0,
      duration: 1000,
      list: [{
        total: 0,
        max: 5500,
      },{
        total: 0,
        max: 550,
      },{
        total: 0,
        max: 75,
      },{
        total: 0,
        max: 100,
      }],
    };
  },
  methods: {
    easeOutQuad(t) {
      return t * ( 2 - t );
    },
    start() {
      this.r = performance.now();
      this.increment();
    },
    increment() {
      cancelAnimationFrame(this.af);
      const t = Math.min(1, (performance.now() - this.r) / this.duration);
      this.list.forEach((item) => {
        const r = Math.min(1, t / (Math.pow(item.max, 1/3) / this.max));
        item.total = item.max * this.easeOutQuad(r);
      });

      if (t < 1) this.af = requestAnimationFrame(this.increment);
    },
    localise(n) {
      return n > 1000 ? (n / 1000).toFixed(1) + 'k' : Math.round(n); 
    },
  },
  mounted() {
    this.max = this.list.reduce((_, item) =>
      Math.max(Math.pow(item.max, 1/3), _), 0);

    setTimeout(this.start, 1000);
  },
}
</script>

