<template>
  <div class="p-analysis" :data-tracing="tracing" :data-complete="complete">
    <img class="bg" :src="`${$router.options.base}bg/analysis-img-03.jpg`">
    <canvas ref="canvas" @mousedown="onmovestart" class="canvas" width="1920" height="1080"></canvas>

    <nav>
      <div class="nav-left">
        <img class="tools" :src="`${$router.options.base}icons/tools.svg`">
      </div>
      <div class="nav-right">
        <label class="next" @click="next=true"><span>Done</span></label>
      </div>
      <div class="nav-bottom">
        <ul class="types">
          <li v-for="(item, type) in types" v-bind:key="item.colour">
            <input ref="types" :id="`types-${type}`" name="types" type="radio" :value="type" @change="onselect(type)">
            <label :for="`types-${type}`" :data-colour="item.colour" :style="{ color: item.colour }">
              <img :src="`${$router.options.base}icons/${type}.svg`">
              <span>{{ item.label || (type.charAt(0).toUpperCase() + type.slice(1)) }}</span>
            </label>
          </li>
        </ul>
      </div>
    </nav>

    <div class="overlay" v-if="next">
      <div class="overlay--inner">
        <img :src="`${$router.options.base}icons/tick.svg`">
        <h2>Analysis Received</h2>
        <p>You&rsquo;re now officially a Reef Citizen Scientist. Check out your progress on your dashboard or keep on going.</p>
        <router-link :to="{ name: 'dashboard' }">View dashboard</router-link>
        <router-link :to="{ name: 'dashboard' }">Next image</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Analysis',
  data() {
    return {
      next: false,
      complete: false,
      active: null,
      tracing: false,
      imageRatio: 1,
      colour: null,
      trace: [],
      types: {
        coral: {
          colour: '#87d0f7',
        },
        rubble: {
          colour: '#ffc3ac',
        },
        monument: {
          colour: '#cbcfff',
          label: 'Coral monument',
        },
        sand: {
          colour: '#ffeab6',
          label: 'Sand cover',
        },
        unsure: {
          colour: '#ffb9cd',
          label: 'Don’t know?',
        },
      },
    };
  },
  mounted() {
    window.addEventListener('keydown', this.onkeydown);
    window.addEventListener('keyup', this.onkeyup);
    // this.$refs.coral.checked = true;
    this.$refs.types[0].checked = true;
    this.onselect('coral');
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.onkeydown);
    window.removeEventListener('keyup', this.onkeyup);
  },
  methods: {
    onclear(e) {
      this.trace = e ? this.trace.filter(path =>
        path.slice(-1)[0].type !== this.active) : [];
      this.imagetrace(!!e);
      // props.active.$erase.checked = false;
      // props.active.$erase.disabled = true;
    },
    onkeydown() {
      // if (e.shiftKey && !props.active.$erase.disabled) {
      //   props.active.$erase.checked = true;
      //   if (this.tracing) this.imagetrace();
      // }
    },
    keyup() {
      // if (!e.shiftKey && props.active.$erase.checked) {
      //   props.active.$erase.checked = false;
      //   if (this.tracing) this.imagetrace();
      // }
    },
    onselect(type) {
      this.active = type;
      // props.active = fn.active(this);
      this.imageRatio = this.$refs.canvas.width / this.$refs.canvas.offsetWidth;
      const dim = 20 / this.imageRatio;
      const c = document.createElement('canvas');
      c.width = dim;
      c.height = dim;
      const ctx = c.getContext('2d');
      ctx.beginPath();
      ctx.arc(dim / 2, dim / 2, dim / 2, 0, 2 * Math.PI);
      ctx.closePath();
      ctx.fillStyle = `${this.types[this.active].colour}80`;
      ctx.fill();
      this.$refs.canvas.style.cursor = `url(${c.toDataURL('image/png')}) ${dim / 2} ${dim / 2}, auto`;
      this.imagetrace(true);
    },
    onmove(e, trace = true) {
      this.trace.slice(-1)[0].push([
        Math.max(0, Math.min(e.pageX * this.imageRatio, 1920)),
        Math.max(0, Math.min(e.pageY * this.imageRatio, 1080)),
      ]);
      if (trace) this.imagetrace();
    },
    onmovestart(e) {
      if (this.tracing) return;
      this.tracing = true;
      // const type = props.active.type;
      this.trace.push([]);
      this.onmove(e, false);
      this.imageRatio = this.$refs.canvas.width / this.$refs.canvas.offsetWidth;
      window.addEventListener('mousemove', this.onmove);
      window.addEventListener('mouseup', this.onmoveend);
    },
    onmoveend() {
      this.tracing = false;
      this.trace.slice(-1)[0].push({
        // erase: props.active.$erase.checked,
        colour: this.types[this.active].colour,
        type: this.active,
      });
      window.removeEventListener('mousemove', this.onmove);
      window.removeEventListener('mouseup', this.onmoveend);
      this.imagetrace(true);
    },
    imagetrace(fill) {
      const ctx = this.$refs.canvas.getContext('2d');
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      ctx.lineWidth = 20;
      ctx.lineJoin = 'round';
      ctx.lineCap = 'round';
      this.trace.forEach((path, i) => {
        const active = !fill && i === this.trace.length - 1;
        const options = active ? {
          // erase: props.active.$erase.checked,
          colour: this.types[this.active].colour,
        } : Object.assign({}, path.slice(-1)[0]);
        
        ctx.beginPath();
        path.forEach((pos, ii) => {
          if (ii < path.length - 1 || active) {
            if (ii) ctx.lineTo(pos[0], pos[1]);
            else ctx.moveTo(pos[0], pos[1]);
          }
        });
        ctx.lineWidth = 20;
        ctx.strokeStyle = options.colour;

        if (fill || !active) {
          ctx.save();
          if (options.erase) {
            ctx.globalCompositeOperation = 'destination-out';
          }
          ctx.closePath();
          ctx.stroke();
          ctx.fillStyle = options.colour;
          ctx.fill();
          ctx.restore();
        } else {
          ctx.save();
          if (options.erase) {
            ctx.globalCompositeOperation = 'destination-out';
          }
          ctx.stroke();
          ctx.restore();
          ctx.lineWidth = 2;
          ctx.setLineDash([5, 5]);
          ctx.save();
          if (options.erase) {
            ctx.strokeStyle = options.colour;
          } else {
            ctx.globalCompositeOperation = 'destination-out';
            ctx.strokeStyle = '#FFFFFF';
          }
          ctx.stroke();
          ctx.restore();
        }
      });
      
      if (fill) {
        const c = document.createElement('canvas');
        c.width = 100;
        c.height = c.width * ctx.canvas.height / ctx.canvas.width;
        const ctx2 = c.getContext('2d');
        ctx2.drawImage(this.$refs.canvas, 0, 0, c.width, c.height);
        const imageData = ctx2.getImageData(0, 0, c.width, c.height);
        const data = imageData.data;
        const l = data.length / 4;
        const n = {};
        let hex;
        let hexTotal = 0;
        for (let i=0;i<l;++i) {
          if (data[i * 4 + 3]) {
            hex = data.slice(i * 4, i * 4 + 3).reduce((r, c) => r.concat(c.toString(16).padStart(2, '0')), []).join('');
            n[`#${hex}`] = (n[`#${hex}`] || 0) + 1;
            ++hexTotal;
          }
        }
        this.complete = hexTotal / l > 0.5;
        // $el.selects.forEach(($select) => {
        //   const active = fn.active($select);
        //   const p = Math.round(100 * (n[active.colour.toLowerCase()] || 0)/l);
          
        //   active.$output.value = `${p}%`;
        //   active.$slider.value = p;
        //   active.$slider.dataset.trace = p || '';
        //   active.$erase.disabled = !p;
        // });
      }
    },
  },
}
</script>
