import Vue from 'vue';
import Router from 'vue-router';

import Home from '$page/Home';
import Analysis from '$page/Analysis';
import Dashboard from '$page/Dashboard';
import Share from '$page/Share';

import { publicPath as base } from '../../../vue.config.js'

Vue.use(Router);

const routes = [
  {
    name: 'home',
    path: '/',
    component: Home,
  },
  {
    name: 'analysis',
    path: '/analysis',
    component: Analysis,
  },
  {
    name: 'dashboard',
    path: '/dashboard',
    component: Dashboard,
  },
  {
    name: 'share',
    path: '/share',
    component: Share,
  },
];

const $router = new Router({
  base,
  mode: 'history',
  routes,
  scrollBehavior(to) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
    return { x: 0, y: 0 };
  },
  linkActiveClass: 'is-active',
});

export default $router;