<template>
  <div id="app" ref="app" :data-route="$router.currentRoute.name" :data-path="$router.currentRoute.path.replace(/^\/|\/$/g, '')">
    <div ref="top" class="c-hero__intersect"></div>
    <main class="c-main" id="main" role="main">
      <router-view></router-view>
    </main>
  </div>
  <!-- /o-body-wrap -->
</template>

<script>

export default {
  name: 'app',
  mounted() {
    window.addEventListener('keydown', this.onkeydown);
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.onkeydown);
  },
  methods: {
    onkeydown(e) {
      if (e.keyCode !== 70 || !e.shiftKey) return;
      if (this.fs) {
        this.fs = false;
        document.exitFullscreen();
      } else {
        this.fs = true;
        document.body.requestFullscreen();
      }
    },
  },
}
</script>

